.ddb-searchlist__item {
    align-items: center;
}

.ddb-searchlist__header {
    margin: 0 0 4px;
    line-height: 1;
}

.ddb-searchlist__header a {
    position: relative;
    text-decoration: none;
    color: #505050;
    transition: color 110ms cubic-bezier(0.2, 0, 0.38, 0.9);
}

.ddb-searchlist__header a:hover {
    color: #808285;
}

.ddb-searchlist__buttons {
    display: flex;
    flex-direction: row;
}

.ddb-searchlist__button {
    flex-basis: content;
}
